import { useForm } from "react-hook-form";
import ModalContainer from "../../components/ModalContainer";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup"
// import { useState } from "react";
import {useDispatch, useSelector} from "react-redux"
import {post} from "../../application/middlewares/index"
import toast from "react-hot-toast";
import { useEffect } from "react";

function Form({visible, setCloseModal}){

    const {register, watch, setValue, formState:{errors}, handleSubmit, reset, getValues} = useForm({
        resolver:yupResolver(schema)
    })
    const dispatch = useDispatch()
    const token = useSelector(state => state.auth.token)

    const onSubmit = (data) =>{
        console.log(data)
        dispatch(post({
            url:`api/admin/add-sale`,
            token,
            data,
            action: (response) =>{
                if(response.success){
                    console.log(response.data)
                    toast.success("Hey yu have added sale")
                }else{
                    toast.error("hey yo dont add")  
                }
            }
        }))
        
    }

    useEffect(()=>{
        if(getValues("discount_type_id") === "4"){        
            setValue("min_value", 2);
            setValue("coupon", "wrwerw")
        }
        if(getValues("discount_type_id") === "3"){
            setValue("min_value", "" );
            setValue("coupon", "abs")
        }
    }, [watch("discount_type_id")]) // eslint-disable-line react-hooks/exhaustive-deps

    return (
        <ModalContainer size = "md"
            visible = {visible}
            setCloseModal = {()=>{
                setCloseModal();
                reset({});
            }}
            title = {"Добавление скидки"}
        >
        <form onSubmit = {handleSubmit(onSubmit)} className = "w-96">
            <div className="flex flex-col w-full relative mb-6 ">
                <label>Выберите тип скидки</label>
                <select value = {watch("discount_type_id")} onChange = {(e)=>setValue("discount_type_id", e.target.value)} className = {`${errors.discount_type_id ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}>
                    <option key = {0} value = {0} >Тип скидки</option>
                    <option key = {3} value = {3} >Заказ</option>
                    <option key = {4} value = {4} >Купон</option>
                </select>
                <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                    {errors.discount_type_id?.message}
                </p>
                {
                    watch("discount_type_id") === "4" &&
                
                <div className="relative w-full mt-2">
                    <label>Купон</label>
                    <input type="text" {...register("coupon")}
                        autoComplete="false"
                        className={`${errors.coupon ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                        placeholder="Например Купон для вас"
                    />
                    <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                        {errors.coupon?.message}
                    </p>
                </div>
                } 
                {
                    watch("discount_type_id") === "3" &&
                
                <div className="relative w-full mt-2">
                    <label>Минимальная сумма</label>
                    <input type="text" {...register("min_value")}
                        autoComplete="false"
                        className={`${errors.min_value ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                        placeholder="Например 200"
                    />
                    <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                        {errors.min_value?.message}
                    </p>
                </div>
                }  
                <div className="relative w-full mt-2">
                    <label>Скидка (в процентах)</label>
                    <input type="text" {...register("discount_value")}
                        autoComplete="false"
                        className={`${errors.discount_value ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                        placeholder="Например 50"
                    />
                    <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                        {errors.discount_value?.message}
                    </p>
                </div>
                <div className = "py-3 flex flex-col">
                    <label className = "pl-2 pb-3">Введите начальное число</label>
                    <input {...register("start_date")} type = "text" onFocus = {(e)=>{e.currentTarget.type="date"}} onBlur={(e)=>e.currentTarget.type="text"}  placeholder = "Введите начальное число" className = "ring-indigo-600 px-3 shadow-inner h-12 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2"/>        
                    <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                        {errors.start_date?.message}
                    </p>
                </div>
                <div className = "py-3 flex flex-col">
                    <label className = "pl-2 pb-3">Введите начальное число</label>
                    <input {...register("end_date")} type = "text" onFocus = {(e)=>{e.currentTarget.type="date"}} onBlur={(e)=>e.currentTarget.type="text"}  placeholder = "Введите начальное число" className = "ring-indigo-600 px-3 shadow-inner h-12 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2"/>        
                    <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                        {errors.end_date?.message}
                    </p>
                </div>
            </div>
            <div className = "pt-5 w-full px-1">
                <button type = "submit" className="w-full rounded-xl text-center py-3 bg-green-400 text-white focus:outline-none">
                        Подтвердить
                </button>
            </div> 
        </form>
        </ModalContainer>
 
    )
}

const schema = Yup.object().shape({
    discount_type_id: Yup.number("Обязательное поле").required("Обязательное поле").min(3).max(4),
    start_date:Yup.date("Должно быть число").required("Обязательное поле"),
    end_date:Yup.date("Должно быть число").required("Обязательное поле"),
    min_value:Yup.number("Должна быть строка").required("Обязательное поле").min(1),
    coupon:Yup.string("Должна быть строка").required("Обязательное поле").min(3).max(150),
    discount_value: Yup.number("").required().min(1).max(100)
})
