//     discount_type_id: Yup.number("Обязательное поле").required("Обязательное поле").min(3).max(4),
//     start_date:Yup.date("Должно быть число").required("Обязательное поле"),
//     end_date:Yup.date("Должно быть число").required("Обязательное поле"),
    
    
//     discount_value: Yup.number("").required().min(1).max(100)
// })
export default Form;